export function editRecord ({
  type,
  num,
  staff,
  time = dayjs()
}: {
  type: string;
  num: number;
  staff: string;
  time?: any;
}) {
  return new Promise((resolve, reject) => {
    let operationRecorddList = store.get('OPERATION_RECORD_LIST') || []
    let targetRecord = operationRecorddList.find((item: IRecord) => {
      let ifSameStaff = staff ? staff === item.staff : true
      return (
        dayjs(time).isSame(dayjs(item.time)) &&
        type === item.type &&
        ifSameStaff
      )
    })
    if (targetRecord) {
      targetRecord.num = num
      store.set('OPERATION_RECORD_LIST', operationRecorddList)
      Notification.success({
        message: `${type}   ${num} `,
        title: ''
      })
      resolve()
    } else {
      return pieceRecord({ type, num, staff, time })
    }
  })
}

export function deleteRecord ({
  type,
  num,
  staff,
  time = dayjs()
}: {
  type: string;
  num: number;
  staff: string;
  time?: any;
}) {
  return new Promise((resolve, reject) => {
    let operationRecorddList = store.get('OPERATION_RECORD_LIST') || []
    let targetIndex = operationRecorddList.findIndex((item: IRecord) => {
      let ifSameStaff = staff ? staff === item.staff : true
      return (
        dayjs(time).isSame(dayjs(item.time)) &&
        type === item.type &&
        ifSameStaff
      )
    })
    if (targetIndex !== -1) {
      operationRecorddList.splice(targetIndex, 1)
      store.set('OPERATION_RECORD_LIST', operationRecorddList)
      Notification.success({
        message: ``,
        title: ''
      })
      resolve()
    } else {
      return MessageBox.alert('','')
    }
  })
}
