import G2 from '@antv/g2'
import NP from 'number-precision'

export default function () {
  const chart = new G2.Chart({
    container: 'chart',
    forceFit: true,
    height: 600
  })
  chart.source([])
  // 
  chart.legend({
    // custom: true,
    clickable: false,
    items: [
      { value: '', fill: '#1890FF', marker: 'square' },
      { value: '', fill: '#8c8c8c', marker: 'square' }
    ]
  })
  chart.interval()
  .position('time*num*type')
  .style({
    cursor: 'pointer'
  })
  .color('time', function (time: any) {
    if (time === '') {
      return '#8c8c8c'
    }
    return '#1890FF'
  })
  .color('type',function (type: any) {
    if (type === '') {
      return '#8c8c8c'
    }
  })
  .tooltip('time*num*type', function (time: any, num: any,type: any) {
    if (Array.isArray(num)) {
      return {
        name: type,
        value: NP.minus(num[1],num[0])
      }
    }
    return {
      name: type,
      value: num
    }
  })
  .shape('waterfall')
  chart.render()
  return chart
}
